function Description(){
    const { machineName } = useParams();
    const machine = machineData[machineName] || {};
    const [showSuggestions, setShowSuggestions] = useState(false); // State for modal visibility
    const [bulbColors, setBulbColors] = useState({});
    const [currentGraphIndex, setCurrentGraphIndex] = useState(0);
    const [chartData, setChartData] = useState({});

    useEffect(() => {
        const newBulbColors = {};
        if (machine.components) {
            Object.entries(machine.components).forEach(([componentName, componentData]) => {
                const expiryDate = new Date(componentData.memoryExpiryDate);
                const today = new Date();
                const timeDifference = expiryDate.getTime() - today.getTime();
                const daysDifference = Math.ceil(timeDifference / (1000 * 3600 * 24));

                if (daysDifference <= 30) {
                    newBulbColors[componentName] = "red";
                } else {
                    newBulbColors[componentName] = "green";
                }
            });
        }
        setBulbColors(newBulbColors);
    }, [machine.components]);

    useEffect(() => {
        const generateDetailedData = () => {
            const labels = Array.from({ length: 30 }, (_, i) => {
                const date = new Date();
                date.setDate(date.getDate() - i);
                return date.toLocaleDateString();
            }).reverse();

            return Array.from({ length: 30 }, (_, i) => {
                // Generate values with a random variation around a base value
                const baseValue = 70; // A reasonable base value
                return Math.min(100, Math.max(0, baseValue + Math.floor(Math.random() * 21) - 10)); // Random variation of 10
            });
        };

        const newChartData = {};
        if (machine.components) {
            Object.entries(machine.components).forEach(([componentName]) => {
                newChartData[componentName] = {
                    labels: Array.from({ length: 30 }, (_, i) => {
                        const date = new Date();
                        date.setDate(date.getDate() - i);
                        return date.toLocaleDateString();
                    }).reverse(),
                    values: generateDetailedData()
                };
            });
        }
        setChartData(newChartData);
    }, [machine.components]);

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentGraphIndex(prevIndex => {
                const componentNames = Object.keys(chartData);
                const numComponents = componentNames.length;
                return (prevIndex + 2) % numComponents;
            });
        }, 5000); // Change graph every 5 minutes

        return () => clearInterval(interval);
    }, [chartData]);

    const handleSuggestionsClick = () => {
        setShowSuggestions(true);
    };

    const closeSuggestions = () => {
        setShowSuggestions(false);
    };

    const componentNames = Object.keys(chartData);

    // Get the current and next two component names
    const currentComponents = componentNames.slice(currentGraphIndex, currentGraphIndex +2);
    const nextComponents = componentNames.slice(currentGraphIndex + 2, currentGraphIndex + 2);


    return (
        <div className="description-container">
            <h1>Description Page</h1>
            {/* {machine.video && (
                <video width="90%" height="300" controls autoPlay loop muted>
                    <source src={machine.video} type="video/mp4" />
                    Your browser does not support the video tag.
                </video>
            )} */}
            <div className="chart-slider">
                {currentComponents.length > 0 && (
                    <>
                        {currentComponents.map(componentName => (
                            <ComponentChart
                                key={componentName}
                                componentName={componentName}
                                data={chartData[componentName]}
                            />
                        ))}
                    </>
                )}
            </div>
            <div className="chart-slider">
                {nextComponents.length > 0 && (
                    <>
                        {nextComponents.map(componentName => (
                            <ComponentChart
                                key={componentName}
                                componentName={componentName}
                                data={chartData[componentName]}
                            />
                        ))}
                    </>
                )}
            </div>
            <p><strong>Machine:</strong> {machineName}</p>
            <p><strong>Description:</strong> {machine.description || "No description available."}</p>
            <p><strong>Purchase Date:</strong> {machine.purchaseDate || "No purchase date available."}</p>

            {/* Components Info */}
            <div className="components-info">
                <h2>Components</h2>
                {machine.components ? (
                    Object.entries(machine.components).map(([componentName, componentData]) => (
                        <div key={componentName} className="component">
                            <h3>{componentName}</h3>
                            <p><strong>Description:</strong> {componentData.description}</p>
                            <p><strong>Memory Health:</strong> {componentData.memoryHealth}</p>
                            <p><strong>Memory Expiry Date:</strong> {componentData.memoryExpiryDate}</p>
                            <p><strong>Expected Repair Date:</strong> {componentData.expectedRepairDate}</p>
                            <div>
                            <div className={`bulb ${bulbColors[componentName] === "red" ? "blinking-bulb" : ""}`} style={{ backgroundColor: bulbColors[componentName] }}></div></div>
                            {bulbColors[componentName] === "red" && (
                                <button onClick={handleSuggestionsClick} className="suggestions-button">Suggestions</button>
                            )}
                        </div>
                    ))
                ) : (
                    <p>No component information available.</p>
                )}
            </div>

            {/* Modal for Suggestions */}
            {showSuggestions && (
                <div className="popup-container">
                    <div className="popup-content">
                        <button className="popup-close-button" onClick={closeSuggestions}>X</button>
                        <p>Here are some tips to make the machine part better:</p>
                        <ul>
                            <li>Tip 1: Regular maintenance is key.</li>
                            <li>Tip 2: Monitor the battery power frequently.</li>
                            <li>Tip 3: Keep the memory chip clean.</li>
                            <li>Tip 4: Replace parts nearing their expiry date.</li>
                        </ul>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Description;
