import React, { useState, useEffect } from 'react';
import './Slider.css'; // Create this file for styling
import image from "../images/image3.webp"
import image1 from "../images/image2.jpg"
import image2 from "../images/image4.jpeg"
import image3 from "../images/image5.jpeg"
import image4 from "../images/image6.jpeg"
const slides = [
    { id: 1, imageUrl: image1, caption: '1', description: 'This is the description for slide 1' },
    { id: 2, imageUrl: image, caption: '2', description: 'This is the description for slide 2' },
    { id: 3, imageUrl: image2, caption: '3', description: '.' },
    { id: 4, imageUrl: image3, caption: '4', description: '.' },
    { id: 5, imageUrl: image4, caption: '5', description: '.' },
  ];

function Slider() {
  const [currentSlide, setCurrentSlide] = useState(0);

  useEffect(() => {
    const slideInterval = setInterval(() => {
      setCurrentSlide(prevSlide => (prevSlide + 1) % slides.length);
    }, 4000); // Change slide every 5 seconds

    return () => clearInterval(slideInterval); // Clean up interval on component unmount
  }, []);

  return (
    <div className="slider-container">
      <div className="slider-wrapper" style={{ transform: `translateX(-${currentSlide * 100}%)` }}>
        {slides.map((slide, index) => (
          <div
            className="slide"
            key={index}
            style={{ backgroundImage: `url(${slide.imageUrl})` }}
          >
            <div className="description">{slide.description}</div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Slider;
