import React, { useState } from 'react';
import './header.css';
import catlogo from '../images/cat_logo.png';

function Header() {
    const [isLoggedIn, setIsLoggedIn] = useState(false);

    const handleSignIn = () => {
        // Handle sign-in logic
        console.log('Sign In button clicked');
    };

    const handleLogin = () => {
        // Handle login logic
        console.log('Login button clicked');
        setIsLoggedIn(true); // Assuming login is successful
    };

    const handleLogout = () => {
        // Handle logout logic
        console.log('Logout button clicked');
        setIsLoggedIn(false); // Set the user to logged out
    };

    return (
        <div id="headerdiv">
            <img src={catlogo} alt="Logo" />
            <div>
                {!isLoggedIn ? (
                    <>
                        <button className="auth-button" onClick={handleSignIn}>Sign In</button>
                        <button className="auth-button" onClick={handleLogin}>Login</button>
                    </>
                ) : (
                    <button className="logout-button" onClick={handleLogout}>Logout</button>
                )}
            </div>
        </div>
    );
}

export default Header;
