import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import Header from './components/Header';
import Slider from './components/Slider';
import LandingPage from './pages/landing';
import DescriptionPage from './components/Description'; // Assuming you have created this page

function App() {
  return (
    <Router>
      <div className="App">
        <Header />
        
        <Routes>
          <Route path="/" element={<LandingPage />} />
          <Route path="/description/:machineName" element={<DescriptionPage />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
