import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './landing.css';
import $ from 'jquery'; // Import jQuery
import 'datatables.net'; // Import DataTables JS
import Slider from '../components/Slider';
// import Excavator_1 from '../images/Excavator.jpg';
// import Asphalt_Paver from '../images/Asphalt_Paver.jpeg';
// import Backhoe_Loader from '../images/Backhoe_Loader.jpg';
// import Dozer from '../images/Dozer.jpg';
// import Articulated_Truck from '../images/Articulated_Truck.jpg';


function LandingPage() {
    const [data, setData] = useState([]);
    const [error, setError] = useState(null);
    const navigate = useNavigate();
    useEffect(() => {
        fetch('/Data_set.csv')
            .then(response => {
                if (!response.ok) {
                    throw new Error('Network response was not ok ' + response.statusText);
                }
                return response.text();
            })
            .then(csvData => {
                // console.log('CSV data:', csvData);
                const rows = csvData.split('\n').map(row => row.trim()).filter(row => row.length > 0);
                const headers = rows[0].split(',');
                const rowData = rows.slice(1).map(row => row.split(','));

                const tableData = rowData.map(row => {
                    const obj = {};
                    headers.forEach((header, index) => {
                        obj[header.trim()] = row[index].trim();
                    });
                    return obj;
                });
                const uniqueTableData = tableData.filter((value, index, self) => 
                    index === self.findIndex((t) => (
                        t.Machine === value.Machine
                    ))
                );
                
                setData(uniqueTableData);
                // setData(tableData);
            })
            .catch(error => {
                console.error('Error fetching CSV file:', error);
                setError(error.message);
            });
    }, []);

    
    const handleBoxClick = (machineName) => {
        navigate(`/description/${machineName}`); // Navigate to description page
    };
    const formatMachineName = (name) => {
        return name.replace(/_\d+$/, '');
    };
    
    const getImageSrc = (machine) => {
        return `../images/${formatMachineName(machine)}.jpg`;
    };
    
    return (
        <div id="Landing">
            <Slider />
            {error && <p>Error: {error}</p>}
                <div id="machines">      
                    {data.length > 0 ? (
                        data.map((row, index) => (
                            <div className='machinebox' onClick={() => handleBoxClick(row.Machine)}
                            >    
                                <div className='machineimgdiv'>
                            <img src={getImageSrc(row.Machine)} alt={formatMachineName(row.Machine)} className='machineimg'/>
                                    </div>
                                <div className='machinecontdiv'>
                            <p>{row.Machine}</p>
                            </div>
                           </div>     
                        ))
                    ) : (
                            <p>No data available</p>
                    )}
                </div>
        </div>
    );
}

export default LandingPage;
